header #page_header {
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgb(30, 30, 30);
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.6);
  overflow: hidden;
  transition: 0.4s ease;
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
header #page_logo {
  height: 90px;
  transition: all 1s;
}
header nav {
  width: 100%;
}
header nav .page_nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 902px) {
  header nav .page_nav {
    position: absolute;
    left: 0;
    top: 95px;
    flex-direction: column;
    background-color: white;
    width: 100%;
    padding-bottom: 20px;
    display: none;
  }
  header nav .page_nav li {
    margin: 0 15px;
  }
  header nav .page_nav li a {
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    color: #d3d3d3;
    transition: color 0.4s ease;
  }
  header nav .page_nav li a :hover {
    color: #7f78ac;
  }
}
header nav .hamburger_btn_conatiner {
  margin-left: 90%;
}
header nav .hamburger_btn_conatiner .hamburger {
  display: none;
}
@media (max-width: 902px) {
  header nav .hamburger_btn_conatiner .hamburger {
    display: block;
    margin: 0;
    padding: 0;
    border: 0;
    background-color: transparent;
    cursor: pointer;
  }
  header nav .hamburger_btn_conatiner .hamburger:focus > .hamburger_container {
    box-shadow: 0 0 2px 2px #03e9f4;
  }
  header nav .hamburger_btn_conatiner .hamburger:focus, header nav .hamburger_btn_conatiner .hamburger_container:focus {
    outline: none;
  }
  header nav .hamburger_btn_conatiner .hamburger_container {
    display: flex;
    align-items: center;
    position: relative;
    width: 35px;
    height: 30px;
  }
  header nav .hamburger_btn_conatiner .hamburger_bars {
    position: absolute;
    width: 35px;
    height: 2px;
    background-color: #c700ff;
    transition: transform 220ms ease-in-out;
  }
  header nav .hamburger_btn_conatiner .hamburger_bars::before, header nav .hamburger_btn_conatiner .hamburger_bars::after {
    display: block;
    position: absolute;
    width: 35px;
    height: 2px;
    background-color: #c700ff;
    content: "";
  }
  header nav .hamburger_btn_conatiner .hamburger_bars::before {
    top: -12px;
    transition: top 100ms 250ms ease-in, transform 220ms ease-in-out;
  }
  header nav .hamburger_btn_conatiner .hamburger_bars::after {
    bottom: -12px;
    transition: bottom 100ms 250ms ease-in, transform 220ms ease-in-out;
  }
  header nav .hamburger_btn_conatiner .hamburger_active .hamburger_bars {
    transform: rotate(225deg);
    transition: transform 220ms 120ms ease-in-out;
  }
  header nav .hamburger_btn_conatiner .hamburger_active .hamburger_bars::before {
    top: 0;
    transition: top 100ms ease-out;
  }
  header nav .hamburger_btn_conatiner .hamburger_active .hamburger_bars::after {
    bottom: 0;
    transform: rotate(-90deg);
    transition: bottom 100ms ease-out, transform 220ms 120ms ease-in-out;
  }
}
