  margin: 20px;
  padding: 15px 30px;
  text-align: center;
  text-transform: uppercase;
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}
#form .form_info .form_submit_button:hover {
  box-shadow: 0 0 40px 40px rgba(0, 0, 0, 0.4392156863) inset;
}
#form .form_info .form_submit_button:hover:focus {
  color: #e0e0e0;
  outline: 0;
}
#form .form_info .contact_phone > div > input {
  height: 40px;
  width: 100%;
  outline: 1px solid #03e9f4;
  transition: outline-offset 0.5s ease;
  padding: 8px;
}
#form .form_info .contact_phone > div > label {
  margin-left: 5px;
  font-size: 10px;
}
#form .form_info #date {
  cursor: pointer;
}
#form .form_info #time {
  width: 100%;
  background: rgba(115, 115, 115, 0.22);
  border: 1px solid #03e9f4;
  height: 40px;
  cursor: pointer;
}
#form .form_info #time select {
  appearance: none;
  border: 0;
  box-shadow: none;
}
#form .form_info #time select option {
  color: rgba(0, 0, 0, 0.4392156863);
  background-color: #5d93b6;
  cursor: pointer;
}
#form .form_info #time select option:hover:focus:active {
  background: rgba(115, 115, 115, 0.67);
  cursor: pointer;
}
#form .form_info #time p {
  font-size: 8px;
  letter-spacing: 1px;
}
#form .form_info .contact_phone {
  display: none;
}
#form .form_info .contact_message {
  display: block;
}

.contact_us_space {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 100px;
  align-items: center;
  background-color: #e0e0e0;
  margin-bottom: 20px;
}
.contact_us_space .contact_us {
  padding: 0 5%;
  cursor: pointer;
  height: 100%;
}
.contact_us_space .contact_us .contact_us_click {
  font-size: 20px;
  text-transform: uppercase;
  height: 100%;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}
.contact_us_space .contact_us .contact_us_click:hover {
  background-color: rgba(115, 115, 115, 0.22);
}
.contact_us_space > span {
  height: 100%;
  max-height: 100%;
  width: 1px;
  background-color: rgb(30, 30, 30);
}